.cp-upload_photo .photo_wrap {
  position: relative;
  width: 500px;
}
.cp-upload_photo .photo_wrap .upload_box {
  position: relative;
  width: 100%;
}
.cp-upload_photo .photo_wrap .upload_box .el-upload {
  vertical-align: top;
  width: 100%;
}
.cp-upload_photo .photo_wrap .upload_box .el-upload img {
  vertical-align: top;
  width: 100%;
  height: auto;
}
.cp-upload_photo .photo_wrap .remove_btn {
  line-height: 1;
  position: absolute;
  right: 0;
  top: 0;
  margin-right: -0.5em;
  margin-top: -0.5em;
}



