.uploadimg_wrap {
  position: relative;
}
.uploadimg_wrap img {
  width: 50%;
}
.uploadimg_wrap i {
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform: translate(50%, -50%);
      -ms-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  cursor: pointer;
}



