.header {
  padding: 12px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #ccc;
  -webkit-box-shadow: 1px 2px 3px #ccc;
          box-shadow: 1px 2px 3px #ccc;
}
.header img {
  vertical-align: middle;
  margin-right: 10px;
}
.header .name {
  font-size: 20px;
  font-weight: bold;
}
.header .sign {
  float: right;
}
.header .sign span {
  padding: 2px 10px;
  line-height: 20px;
  display: inline-block;
  background: #f80;
  color: white;
  margin-left: 20px;
}
.header .sign a {
  color: black;
}
.header .exit_btn {
  cursor: pointer;
}



